.footer {
    color: #fff;
    z-index: 5;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: -1;
        background: center bottom / contain no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" fill="none" preserveAspectRatio="none" viewBox="0 0 1440 185"><path d="M1440 121v43.6c-146.2-5.4-280.9-26-413.9-50.9-55.701-10.4-111.201-21.6-167.101-32.8-12.4-2.5-24.9-5-37.3-7.4-31.2-6.2-62.5-12.3-94.2-18.2-120.7-22.5-245.9-42.1-382.3-51 13.7-.4 27.7-.7 42.5-.7C781.899 5.3 1081 110.7 1440 121Z" fill="%23BBB"/><path d="M1440 164.7v20.5H0V18.8C97.3 5.4 158.5 0 228.2 0c14.8 0 29.9.3 45.8.7 24.1.9 47.8 2.1 71.2 3.6 136.4 8.9 261.6 28.5 382.3 51 31.7 5.9 63 12 94.2 18.2 12.5 2.5 24.9 5 37.3 7.4 55.9 11.2 111.3 22.4 167.1 32.9 133 24.9 267.7 45.5 413.9 50.9Z" fill="url(%23a)"/><defs><linearGradient id="a" x1="0" x2="1440" y1="92.605" y2="92.605" gradientUnits="userSpaceOnUse"><stop stop-color="%233483C7"/><stop offset="1" stop-color="%23073773"/></linearGradient></defs></svg>');
        @media (max-width: $phone + px) {
            background: linear-gradient(200deg, #3483C7, #073773);
        }
    }
    // .footer__container
    &__container {
        padding-top: 34px;
        padding-bottom: 17px;
        position: relative;
        @media (max-width: $smallPhone + px) {
            padding-top: 20px;
            padding-bottom: 10px;
        }
    }
    &__content {
    }
    // .footer__logo
    &__logo {
        margin-bottom: 20px;
        @include size(185, 45);
        display: block;
        @media (max-width: 600 + px) {
            // margin-bottom: 0;
        }
        @media (max-width: $smallPhone + px) {
            // @include size(102, 25);
            margin-bottom: 20px;
        }
        img {
            max-width: 100%;
        }
    }
    // .footer__menu
    &__menu {
        margin-bottom: 30px;
        max-width: pc(680, $maxWidthContainer);
        @media (max-width: $phone + 300+ px) {
            max-width: 50%;
            margin-bottom: 20px;
        }
        @media (max-width: 768px) {
            max-width: none;
        }
        @media (max-width: $smallPhone + px) {
            display: none;
        }
        .menu__list {
            gap: 15px;
        }
        .menu__link {
            @media (max-width: $phone + px) {
                padding: 10px 0;
                display: block;
                &::before {
                    background-color: transparent;
                }
            }
            &--active {
                &::before {
                    background-color: #fff;
                    @media (max-width: $phone + px) {
                        background-color: transparent;
                    }
                }
            }
            @media (any-hover: hover) {
                &:hover {
                    &::before {
                        background-color: #fff;
                    }
                }
            }
        }
    }
    // .footer__contacts
    &__contacts {
        max-width: 170px;
        @media (max-width: $phone  + 100 + px) {
            max-width: none;
        }
        @media (max-width: 600 + px) {
            display: none;
        }
    }
    // .footer__address
    &__address {
    }
    // .footer__address-phone
    &__address-phone {
    }
    // .footer__lower-footer
    &__lower-footer {
        display: flex;
        align-items: end;
        justify-content: space-between;
        gap: 15px 30px;
        max-width: pc(680, $maxWidthContainer);
        font-size: 14px;
        line-height: 1.4;
        @media (max-width: $phone + 300+ px) {
            justify-content: start;
            max-width: 85%;
        }
        @media (max-width: $phone + px) {
            max-width: 100%;
            flex-wrap: wrap;
        }
        @media (max-width: $smallPhone + px) {
            flex-wrap: nowrap;
        }
        @media (max-width: 425px) {
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: flex-start;
        }
    }
    &__email {
        display: block;
        margin-top: 10px;
    }
    &__phone {
        display: block;
    }
    // .footer__copyright
    &__copyright {
        flex-grow: 1;
        text-align: right;
        @media (max-width: $phone  + 300 + px) {
            text-align: left;
            flex-grow: 0;
        }
        @media (max-width: 425px) {
            order: 2;
        }
    }
    // .footer__socials
    &__socials {
        color: inherit;
        @media (max-width: 425px) {
            order: 1;
        }
    }
    .social-list__item {
        .social-list__link {
            transition: all 0.2s;
            @media (any-hover: hover) {
                &:hover {
                    color: #ddd;
                    text-shadow: 1px 1px 1px $darkBlue;
                }
            }
            @media (max-width: $phone + px) {
                // @include size(15);
                // flex: 0 0 15px;
                // &::before {
                //     font-size: 6px;
                // }
            }
        }
    }
}
._inner-page,
._single-page {
    .footer {
        @media (min-width: 768.02px) {
            margin-top: -150px;
        }
    }
}
