.index {
    .first-screen {
        overflow: hidden;
        // .first-screen__container
        &__container {
            max-width: 1440px;
            width: 100%;
            height: 636px;
            padding: 0;
            position: relative;
            @media (max-width: $phone + px) {
                height: 345px;
            }
            @media (max-width: 320 + px) {
                // height: 280px;
            }
        }
        // .first-screen__start-block
        &__start-block {
            position: absolute;
            top: pc(147, 636);
            left: 30px;
            background-color: #fff;
            &::before {
                transform: translate(-50%, -50%) rotate(20.83deg);
            }
            @media (max-width: 600 + px) {
                top: 21px;
                left: 5px;
            }
            @media (max-width: 600 + px) {
                top: 10px;
            }
        }
        // .first-screen__title
        &__title {
            color: $darkBlue;
            @media (max-width: $phone + px) {
                margin-bottom: 10px;
            }
        }
        // .first-screen__sub-title
        &__sub-title {
            max-width: 80%;
            @media (max-width: $phone + px) {
                margin-bottom: 15px;
            }
            @media (max-width: $smallPhone + px) {
                margin-bottom: 10px;
            }
        }
        // .first-screen__start-button
        &__start-button {
        }
        // .first-screen__slider
        &__slider {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            // transform: translate(-50%, 0);
            max-width: 1440px;
            height: 100%;
            overflow: hidden;
            @media (max-width: $phone + px) {
                height: 345px;
                max-width: 768px;
            }
            .swiper-wrapper {
                height: 100%;
            }
            .swiper-slide {
                height: 100%;
                flex: 0 0 100%;
            }
            &::before {
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                bottom: -114px;
                height: 366px;
                background: center top no-repeat var(--gradient-line);
                z-index: 1;
                overflow: hidden;
                @media (max-width: $phone + px) {
                    bottom: -229px;
                    width: 100%;
                    background: center top no-repeat var(--gradient-line-tablet);
                }
                @media (max-width: 600 + px) {
                    bottom: -229px;
                    width: 100%;
                    background: center top / 100% 162px no-repeat var(--gradient-line-phone);
                }
                @media (max-width: 600 + px) {
                    height: 340px;
                }
            }
        }
        // .first-screen__thumb-slider
        &__thumbs-slider {
            overflow: hidden;
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            right: 30px;
            z-index: 3;
            // @include size(185, 554);
            width: 185px;
            height: pc(554, 636);
            border-radius: 5px;
            @media (max-width: $phone + px) {
                // @include size(100, 302);
                width: 100px;
            }
            @media (max-width: 600 + px) {
                @include size(100 * 4 + 18 * 3, 68);
                overflow: hidden;
                top: auto;
                right: 50%;
                bottom: 5%;
                transform: translateX(50%);
            }
            @media (max-width: 500 + px) {
                // @include size(288, 45);
                // bottom: 5%;
                width: 90%;
                // height: 45px;
            }
            @media (max-width: $smallPhone + px) {
                // bottom: 5%;
            }
            @media (max-width: 320 + px) {
                bottom: 5%;
                // @include size(288, 45);
            }
            .swiper-wrapper {
                flex-direction: column;
                @media (max-width: 600px) {
                    flex-direction: row;
                }
            }
            .swiper-slide {
                @include size(185, 125);
                // flex: 0 0 125px;
                flex: auto;
                background-color: rgba(255, 255, 255, .8);
                cursor: pointer;
                position: relative;
                border-radius: 5px;
                overflow: hidden;
                margin-bottom: 18px;
                @media (any-hover:hover) {
                    &:not(.swiper-slide-thumb-active):hover {
                        img {
                            opacity: .7;
                        }
                    }
                }
                @media (max-width: $phone + px) {
                    @include size(100, 68);
                    flex: 0 0 68px;
                    margin-bottom: 10px;
                }
                @media (max-width: 600px) {
                    @include size(100, 68);
                    flex: 0 0 calc(25% - 7px);
                    margin-right: 10px;
                    margin-bottom: 0;
                }
                @media (max-width: 500 - 1 +px) {
                    // @include size(66, 45);
                    flex: 0 0 calc(25% - 5px);
                    // padding-top: 25%;
                    margin-right: 5px;
                    margin-bottom: 0;
                }
                img {
                    opacity: 0.4    ;
                    transition: opacity 0.3s;
                    @media (max-width: 500 + px) {
                        @include abs;
                        object-fit: cover;
                    }
                }
                &-thumb-active {
                    cursor: unset;
                    img {
                        opacity: 1;
                    }
                }
            }
        }
        .swiper-wrapper {
            display: flex;
        }
        .swiper-slide {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
